.backdrop {
  z-index: 1;
  opacity: 1;
  width: 100%;
  height: 100%;
  transition: visibility 700ms, opacity 700ms var(--two-time);
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop.is-hidden .mob-menu {
  transform: translate(0%, -100%)scale(0);
}

.mob-menu {
  z-index: 1;
  width: 100vw;
  height: 100vh;
  background-color: #84a178;
  transition: transform .7s;
  position: fixed;
  top: 0;
  left: 0;
}

.logo-ice {
  width: 40px;
  height: 37px;
  position: absolute;
  top: 12px;
  left: 20px;
}

@media screen and (min-width: 768px) {
  .logo-ice {
    width: 48px;
    height: 44px;
    top: 20px;
    left: 32px;
  }
}

.mob-menu-close-btn {
  width: 32px;
  height: 32px;
  fill: #fcfcfc;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 16px 20px 52px auto;
  padding: 0;
  display: block;
}

.mob-menu-close-btn:hover, .mob-menu-close-btn:focus {
  fill: #f8d24e;
}

@media screen and (min-width: 768px) {
  .mob-menu-close-btn {
    margin: 32px 32px 76px auto;
  }
}

.mob-menu-list {
  text-align: center;
}

.mob-menu-list:not(:last-child) {
  margin-bottom: 32px;
}

@media screen and (min-width: 768px) {
  .mob-menu-list:not(:last-child) {
    margin-bottom: 40px;
  }
}

.mob-menu-link {
  color: #fcfcfc;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.2;
}

@media screen and (min-width: 768px) {
  .mob-menu-link {
    font-size: 48px;
    line-height: 1.2;
  }
}

.mob-menu-buy-btn {
  text-align: center;
  color: #131313;
  cursor: pointer;
  background-color: #f8d24e;
  border: none;
  border-radius: 10px;
  margin-top: 112px;
  margin-left: auto;
  margin-right: auto;
  padding: 12px 32px;
  font-size: 14px;
  font-weight: 600;
  line-height: 17px;
  position: absolute;
  bottom: 53px;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (min-width: 768px) {
  .mob-menu-buy-btn {
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
    display: none;
  }
}

.mob-menu-buy-btn:hover, .mob-menu-buy-btn:focus {
  background-color: var(--button-color);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.mob-menu-btn-buy {
  width: 12px;
  height: 12px;
  display: inline-block;
}

